.weather {
  width: 500px;
  border-radius: 50px;
  box-shadow: 10px -2px 20px 2px rgba(0, 0, 0, 0.3);
  color: #fff;
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  margin: 20px auto;
  padding: 0 20px 20px 20px;
  
}

.top,
.bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.city {
  font-weight: 600;
  font-size: 18pxl;
  line-height: 1;
  margin: 0;
  letter-spacing: 1px;
  text-transform: capitalize;
}
.weather-description {
  font-weight: 400;
  font-size: 14px;
  line-height: 1;
  margin: 0;
  text-transform: capitalize;
}
.weather-icon {
  width: 100px;
}

.temp {
  font-weight: 600;
  font-size: 70px;
  width: auto;
  letter-spacing: -5px;
  margin: 10px 0;
}

.details {
  width: 100%;
  padding-left: 20px;
}
.parameter-row {
  display: flex;
  justify-content: space-between;
}
.parameter-label {
  text-align: left;
  font-weight: 400;
  font-size: 12px;
}
.parameter-value {
  text-align: right;
  font-weight: 600;
  font-size: 12px;
}
