.title {
  font-size: 23px;
  font-weight: 700;
  margin-top: 50px;
  margin-bottom: 50px;
  color: #f0eeee;
}
.daily-item {
   background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  border-radius: 15px;
  height: 40px;
  margin: 5px;
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 14px;
  padding: 5px 20px;
}
.icon-small {
  width: 40px;
}
.day {
  color: #fff;
  flex: 1 1;
  font-size: 15px;
  font-weight: 600;
}
.description {
  flex: 1 1;
  margin-right: 15px;
  text-align: right;
  color: aliceblue;
}
.min-max {
  color: #f0eeee;
}

.daily-details-grid {
 grid-row-gap: 0;
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
  column-gap: 15px;
  display: grid;
  flex: 1 1;
  grid-template-columns: auto auto;
  padding: 5px 15px;
  row-gap: 0;
}

.daily-details-grid-item {
  align-items: center;
  display: flex;
  height: 30px;
  justify-content: space-between;
}

.daily-details-grid-item label:first-child {
  color: #757575;
}

.daily-details-grid-item label:last-child {
  color: #a9a9a9;
}